.button {
  display: inline-block;
  margin-bottom: 0;
  font-weight: @btn-font-weight;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
  background-color: @gray-lighter;
  white-space: nowrap;
  transition: background-color 300ms ease-out;
  .user-select(none);
  .button-size(
  	@padding-base-vertical; 
  	(@padding-base-horizontal * 2); 
  	@font-size-base; 
  	@line-height-base; 
  	@border-radius-base
  );
  &,
  &:active,
  &.active {
    &:focus {
      .tab-focus();
    }
  }
  &:hover,
  &:focus {
    color: @btn-default-color;
    text-decoration: none;
  }
  &:active,
  &.active {
    outline: 0;
    background-image: none;
    .box-shadow(inset 0 3px 5px rgba(0,0,0,.125));
  }
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    cursor: not-allowed;
    pointer-events: none;
    .opacity(.65);
    .box-shadow(none);
  }
}

