//
// Button Sizes
// ====================
.button-lg {
  .button-size(
  	@padding-large-vertical; 
  	(@padding-large-horizontal * 2 ); 
  	@font-size-large; 
  	@line-height-large; 
  	@border-radius-large);
}
.button-sm {
  .button-size(
  	@padding-small-vertical; 
  	(@padding-small-horizontal * 2); 
  	@font-size-small; 
  	@line-height-small; 
  	@border-radius-small
  );
}
.button-xs {
  .button-size(
  	@padding-xs-vertical; 
  	(@padding-xs-horizontal * 2); 
  	@font-size-small; 
  	@line-height-small; 
  	@border-radius-small
  );
}

//
// Block button
// ====================
.button-block {
  display: block;
  width: 100%;
  padding-left: 0;
  padding-right: 0;
}
.button-block + .btn-block {
  margin-top: 5px;
}
input[type="submit"],
input[type="reset"],
input[type="button"] {
  &.btn-block {
    width: 100%;
  }
}

